/* Каталог */
/*Общее*/

.product-catalogue {
  width: 1440px;
  margin: 0 auto;
}

.product-catalogue-content {
  display: inline-block;
  width: 915px;
  margin-right: 120px;
}

/*Breadcrumbs*/
.site-path {
  width: 1120px;
  margin: 0 auto;
  padding-bottom: 18px;
}

.site-path__items {
  display: flex;
  padding-top: 20px;
}
.site-path__item {
  list-style: none;
}
.site-path__item a {
  font-size: 14px;
  text-align: center;
  text-decoration: none;
  color: #333333;
}
.site-path__item:not(:first-child)::before {
  content: "\2000 \203a \2000";
}

/*Голова каталога*/
.product-catalogue__head {
  display: flex;
  justify-content: space-between;
  width: 892px;
  height: 28px;
  margin-top: -2px;
  margin-left: 24px;
}

/*.product-catalogue__section-title {
  outline: 1px solid red;
}
*/
.product-catalogue__sort-by {
  text-align: right;
  padding-right: 13px;
}

h2.section-name,
p.sort-by {
  display: inline-block;
}

h2.section-name {
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
  font-size: 18px;
  text-align: left;
  text-transform: uppercase;
}

span.amount {
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 18px;
  text-align: left;
  padding-left: 9px;
}

.sort-by {
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 18px;
  text-align: left;
  padding-right: 6px;
  vertical-align: top;
}

select {
  width: 174px;
  height: 21px;
  border-radius: 5px;
  padding-left: 9px;
  font-size: 14px;
  font-family: Roboto;
  font-weight: 300;
  line-height: normal;
  text-align: center;
  background: url(data:image/png;base64,) no-repeat;
  background-position: 151px center;

  -webkit-appearance: none;
  -moz-appearance: none;
  text-indent: 0.01px; 
  text-overflow: '';
  -ms-appearance: none;
  appearance: none;
}

/*Основная часть каталога с фотками товара*/
.product-catalogue__item-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  width: 892px;
  margin-left: 22px;
  border-top: 1px solid #F4F4F4;
}

/*Сердечки для избранного товара с эффектами*/
.product-catalogue .product-catalogue__product_favorite a,
.product-catalogue .product-catalogue__product_favorite p {
  position: relative;
  top: 1px;
  width: 33px;
  height: 30px;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
}

.product-catalogue .product-catalogue__product_favorite a:hover,
.product-catalogue .product-catalogue__product_favorite p:hover {
  cursor: pointer;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  z-index: 5;
}

.product-catalogue .product-catalogue__product_favorite,
.product-catalogue__product_favorite-chosen {
  position: absolute;
  top: 9px;
  right: 6px;
  z-index: 10;
}

.product-catalogue__product_favorite-chosen {
  position: absolute;
  display: block;
  width: 33px;
  height: 30px;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
}

.product-catalogue__product_favorite-chosen:hover::before {
  position: relative;
  top: 1px;
  left: -133px;
  content: "В избранном";
  display: block;
  width: 133px;
  height: 22px;
  padding: 2px 0 0 12px;
  border: 2px solid #FF748B;
  border-radius: 5px;
  font-family: Roboto;
  font-weight: 300;
  font-size: 18px;
  color: #FF748B;
  cursor: pointer;
}


/* Карточка товара в каталоге с ховером*/
a.item-list__item-card {
  display: inline-block;
  width: 283px;
  height: auto;
  margin-top: 27px;
  margin-bottom: -17px;
  padding-bottom: 53px;
  text-decoration: none;
}

.product-catalogue__item-list:not(.product-catalogue__item-list_favorite) a.item-list__item-card:nth-of-type(3n + 2) {
    margin-left: 20px;
    margin-right: 20px;
}

.product-catalogue .item-list__item-card:hover {
  box-shadow: 2px 2px 2px 2px #cccccc;
}

.product-catalogue .item-list__item-card:hover .sizes {
