/*Пагинация в каталоге*/
.product-catalogue__pagination {
  display: block;
  width: 892px;
  margin: 20px 0 25px 22px;
}

.page-nav-wrapper {
  display: flex;
  width: 365px;
  margin: 0 auto;
  align-items: center;
}

.product-catalogue__pagination ul {
  margin: 0 auto;
  display: flex;
}

.product-catalogue__pagination ul li {
  display: inline-block;
  width: 19px;
  height: 26px;
  vertical-align: middle;
  padding: 5px;
  font-family: 'Roboto', sans-serif;
  font-weight: 900;
  font-size: 14px;
  color: #000000;
}

.product-catalogue__pagination ul li + li {
    margin-left: 25px;
}

li.active {
  display: inline-block;
  padding: 5px;
  background: #D4D4D4;
  border-radius: 7px;
  color: #000000;
}

.angle-back a,
.angle-forward a {
  position: relative;
  display: inline-block;
  top: 0;
  right: -15px;
  width: 26px;
  height: 26px;
  vertical-align: middle;
  background: url(data:image/png;base64,);
  background-position: 80px -107px;
}

.angle-forward a {
  left: -7px;
  background-position: 36px -107px;
}

.hidden {
